@font-face {
  font-family: "Haas R";
  src: url("fonts/NHaasGroteskTXPro-55Rg.ttf");
}
@font-face {
  font-family: "Haas M";
  src: url("fonts/NHaasGroteskTXPro-65Md.ttf");
}
@font-face {
  font-family: "Haas B";
  src: url("fonts/NHaasGroteskTXPro-75Bd.ttf");
}
@font-face {
  font-family: "Grifter";
  src: url("fonts/grifterbold.otf");
}

:root {
  --primary: #5627ff;

  --green: #00bc87;
  --light-green: #bdffe9;
  --blue: #00c2ee;
  --yellow: #ffa800;
  --light-yellow: #ffe7bd;
  --red: #d00090;
  --light-red: #ffbdee;
  --orange: #e74c3c;
  --light-orange: #ffc0bd;
  --dark: #000000;

  --dark-blue: #063d8c;
  --light-color: #b3bfce;

  --light: #f3f3f4;
  --light-0: #fafafa;
  --light-1: #d5e0e2;
  --light-2: #777777;
  --light-3: #585858;
}

* {
  outline: none;
  box-sizing: border-box;
}

html,
body {
  font-family: "Inter";
  overflow-x: hidden;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

.header {
  padding: 20px;
  button {
    border: none;
    background: none;
    margin-left: 1em;
    font-size: 15px;
  }
}

.rounded {
  border-radius: 20px;
}

.carousel {
  margin-top: 30px;
}

.close-btn {
  border: none;
  border-radius: 50px;
  position: absolute;
  right: 20px;
  height: 40px;
  width: 40px;
}

.cert-container {
  height: clamp(550px, 90vh, 90vh);
  display: relative;
  .title {
    font-size: 35px;
    font-family: "Inter";
    font-weight: 700;
    text-transform: capitalize;
    max-width: 600px;
    letter-spacing: 1px;
    color: var(--dark);
  }
  p {
    font-family: "Inter";
    font-size: 15px;
    font-weight: 400;
    color: var(--dark);
    line-height: 25px;
  }
}

.modal-body::-webkit-scrollbar {
  width: 0.7em;
}

.modal-body::-webkit-scrollbar-track {
  background: var(--light-0);
  padding-right: 10px;
}

.modal-body::-webkit-scrollbar-thumb {
  background-color: var(--light-2);
  border-radius: 50px;
  border: solid 2px var(--light-0);
  &:hover {
    background-color: var(--dark);
  }
  &:active {
    cursor: grabbing;
  }
}

.modal-body::-webkit-scrollbar-corner {
  background-color: transparent;
}

.cert {
  margin-bottom: 30px;
  border-radius: 10px;
  border: rgb(0, 0, 0) solid 5px;
}

button {
  &:focus {
    outline: none;
  }
}
a {
  text-decoration: none;
  color: inherit;
  &:hover {
    text-decoration: none;
    color: var(--light-2);
  }
}
.ver {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text-dim {
  color: var(--light-2);
  transition: 0.3s;
  &:hover {
    color: var(--light);
  }
}

.gradient {
  /* background: linear-gradient(
    to right,
    var(--yellow),
    var(--red),
    var(--green),
    var(--blue),
    var(--orange),
    var(--yellow)
  ); */

  background: radial-gradient(
    circle at left,
    #0f42ce,
    #1c79f2,
    #f7a0f5,
    #fdd8b7,
    #f7a0f5,
    #1c79f2,
    #0f42ce
  );

  color: rgb(255, 255, 255);
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  animation: shine 5s infinite linear;
  background-size: 200% auto;
}

@keyframes shine {
  to {
    background-position: -200%;
  }
}

.nav-btn {
  height: 40px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  border-radius: 50px;
  font-weight: 700;
  margin-right: 20px;
  background: none;
  font-size: 15px;
  transition: 0.3s;
  &:hover {
    background: var(--light);
  }

  &.active {
    background: var(--light-blue);
    color: var(--blue);
  }
}

.main {
  h6 {
    font-weight: 700;
    font-size: 20px;
    font-family: "Grifter";
    color: var(--dark);
  }
  h1 {
    margin-top: 70px;
    font-size: 30px;
    font-family: "Inter";
    margin-bottom: 15px;
    color: var(--dark);
    text-transform: capitalize;
  }
  h2 {
    font-size: 20px;
    font-weight: 400;
    color: var(--light-2);
    font-family: "Inter";
    margin-top: 0px;
    margin-bottom: 20px;
  }
}
.call-to-action {
  display: flex;
  justify-content: space-between;
  width: 400px;
  .hire {
    height: 55px;
    padding-left: 40px;
    padding-right: 40px;
    color: white;
    background: var(--dark);
    border: solid 1px var(--dark);
    font-family: "Inter";
    font-weight: 700;
    font-size: 16px;
    border-radius: 10px;
    transition: 0.3s;
    &:hover {
      border: solid 1px var(--light-2);
      background-color: var(--light-2);
      color: white;
    }
  }
  .square {
    height: 50px;
    width: 50px;
    font-size: 22px;
    background: transparent;
  }
}
.square {
  border: none;
  background: var(--light);
  font-size: 16px;
  font-family: "Poppins";
  font-weight: 700;
  border-radius: 10px;
  transition: 0.3s;
  height: 50px;
  width: 50px;
  font-size: 20px;
}

.about {
  /* background: radial-gradient(
    circle at top left,
    #d000924b,
    transparent,
    transparent,
    transparent
  ); */
  padding-top: 20px;
  padding-bottom: 50px;
}

.about-hi {
  border-radius: 20px;
  height: 80px;
  a {
    width: calc(100% - 2px);
    button {
      width: 100%;
      height: 40px;

      color: var(--dark);
      background: var(--light);
      text-align: center;
      border: none;
      background: none;
      height: 80px;
      border-radius: 20px;

      font-size: 15px;
      font-weight: 700;

      i {
        font-size: 25px;
      }

      &:hover {
        transition: 0.3s;
        background: var(--light);
      }
    }
  }
}

@keyframes bg-gradient {
  0% {
    background: radial-gradient(
      circle at top right,
      #ffaa0062,
      transparent,
      transparent,
      #00c2ee69
    );
  }
  20% {
    background: radial-gradient(
      circle at center,
      #ffaa0062,
      transparent,
      transparent,
      #00c2ee69
    );
  }
  40% {
    background: radial-gradient(
      circle at right,
      #ffaa0062,
      transparent,
      transparent,
      #00c2ee69
    );
  }
  60% {
    background: radial-gradient(
      circle at left,
      #ffaa0062,
      transparent,
      transparent,
      #00c2ee69
    );
  }
  80% {
    background: radial-gradient(
      circle at top left,
      #ffaa0062,
      transparent,
      transparent,
      #00c2ee69
    );
  }
  100% {
    background: radial-gradient(
      circle at top right,
      #ffaa0062,
      transparent,
      transparent,
      #00c2ee69
    );
  }
}

.stat {
  padding-top: 60px;
  padding-bottom: 100px;
  margin-top: 100px;
  background-color: var(--light);
  div {
    border: none;
    background: none;
    font-size: 20px;
    font-family: "Poppins";
    color: var(--light-2);
    font {
      font-size: 90px;
      font-family: "Poppins";
      font-weight: 700;
      margin-top: 50px;
      color: var(--dark);
    }
  }
}

@keyframes colorChange {
  0% {
    background-position: 300%;
  }
}

.bolder {
  font-weight: 600;
}

@keyframes shine-top {
  to {
    background-position-y: -200%;
    background-position-x: -200%;
  }
}

/* skills  */

.skills {
  padding-top: 50px;
  padding-bottom: 50px;
  .h1 {
    font-size: 40px;
    font-weight: 900;
    color: var(--dark);
    text-transform: capitalize;
    margin-bottom: 10px;
    font-family: "Grifter";
  }
  .h2 {
    font-size: 17px;
    font-weight: 400;
    color: var(--light-2);
    font-family: "Poppins";
    margin-top: 10px;
    margin-bottom: 20px;
    letter-spacing: 0.2px;
    line-height: 23px;
  }
}
.skill-card-container {
  margin-top: 9px;
}
.skill-cards {
  display: grid;
  gap: 5px;
  grid-template-columns: repeat(auto-fit, minmax(30%, 2fr));
  grid-auto-rows: auto;
}
.skill-card {
  background: white;
  display: flex;
  align-items: center;
  color: var(--dark);
  .icon {
    border: none;
    background: none;
    font-size: 14px;
    background: radial-gradient(
      circle at left,
      #0f42ce,
      #1c79f2,
      #f7a0f5,
      #fdd8b7,
      #f7a0f5,
      #1c79f2,
      #0f42ce
    );

    color: rgb(255, 255, 255);
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    animation: shine 5s infinite linear;
    background-size: 200% auto;
  }
  .language {
    font-family: "Inter";
    font-size: 14px;
    font-weight: 500;
    margin-top: 6.3px;
    margin-left: 5px;
  }
}

/* pc */
@media (min-width: 900px) {
  .mobile {
    display: none;
  }

  body::-webkit-scrollbar {
    width: 0.8em;
  }

  body::-webkit-scrollbar-track {
    background: var(--light-0);
    padding-right: 10px;
  }

  body::-webkit-scrollbar-thumb {
    background-color: var(--dark);
    border-radius: 50px;
    border: solid 2px var(--light-0);
  }

  body::-webkit-scrollbar-corner {
    background-color: transparent;
  }

  .featured {
    padding-bottom: 50px;

    .h1 {
      font-size: 40px;
      font-weight: 700;
      color: var(--dark);
      text-transform: capitalize;
      font-family: "Grifter";
    }
    .h2 {
      font-size: 18px;
      font-weight: 400;
      color: var(--light-2);
      font-family: Poppins;
      margin-top: 10px;
      margin-bottom: 20px;
    }
    .small-text {
      font-size: 18px;
      font-weight: 700;
      font-family: Poppins;
    }

    .project {
      width: 100%;
      margin-bottom: 30px;
      display: flex;
      align-items: center;
      transition: 0.3s;
      padding: 10px;
      border-radius: 20px;
      position: relative;
      background: rgb(255, 255, 255);
      img {
        width: 20%;
        border-radius: 10px;
      }
      .details {
        width: 80%;
        background: white;
        left: 190px;
        padding: 1.5em;
        border-radius: 20px;
        transition: 0.5s;
        .title {
          font-size: 1.5em;
          font-weight: 700;
          color: var(--dark);
          font-family: "Rubik";
          /* set height of work to the actual word size */
        }
        .desc {
          font-size: 1em;
          font-weight: 400;
          color: var(--light-2);
          font-family: "Inter";
        }

        .language-badge {
          border: none;
          font-size: 10px;
          color: var(--light-2);
          font-weight: 600;
          letter-spacing: 0.03em;
          font-family: "Source Code Pro";
          position: relative;
          padding-left: 0.7em;
          padding-right: 0.7em;
          i {
            position: absolute;
            left: 0;
            top: 55%;
            transform: translateY(-50%);
          }
        }
      }
      &:hover {
        box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.062);
      }
    }
  }

  .works {
    padding-bottom: 50px;
    .h1 {
      font-size: 40px;
      font-weight: 700;
      color: var(--dark);
      text-transform: capitalize;
      font-family: "Grifter";
    }
    .h2 {
      font-size: 18px;
      font-weight: 400;
      color: var(--light-2);
      font-family: Poppins;
      margin-top: 10px;
      margin-bottom: 20px;
    }
    .small-text {
      font-size: 18px;
      font-weight: 700;
      font-family: Poppins;
    }

    .work {
      margin-bottom: 30px;
      display: flex;
      align-items: center;
      overflow-y: hidden;
      transition: 0.3s;
      padding: 10px;
      border-radius: 20px;
      position: relative;
      img {
        width: 18%;
        height: 18%;
        border-radius: 10px;
      }
      .details {
        width: 80%;
        padding-left: 2em;
        .title {
          font-size: 1.5em;
          font-weight: 700;
          color: var(--dark);
          font-family: "Rubik";
          /* set height of work to the actual word size */
        }
        .desc {
          font-size: 1em;
          font-weight: 400;
          color: var(--light-2);
          font-family: "Inter";
        }

        .language-badge {
          border: none;
          font-size: 10px;
          color: var(--light-2);
          font-weight: 600;
          letter-spacing: 0.03em;
          font-family: "Source Code Pro";
          position: relative;
          padding-left: 0.7em;
          padding-right: 0.7em;
          i {
            position: absolute;
            left: 0;
            top: 55%;
            transform: translateY(-50%);
          }
        }
      }
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.081);
      &:hover {
        transform: translateY(-5px);
        box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.062);
      }
    }
  }

  .social-bar {
    position: fixed;
    left: 20px;
    bottom: 20px;
    padding: 3px;
    border-radius: 50px;
    background: linear-gradient(to top, #0f43d4, #1c79f2, #f7a0f5);
    background-size: 200% auto;
    /* run infinite animation with interval */

    a {
      color: rgb(255, 255, 255);
      i {
        font-size: 1.2em;
      }
      &:hover {
        color: rgba(255, 255, 255, 0.363);
      }
    }
    .social-bar-links {
      display: grid;
      justify-content: center;
      align-items: center;
      transition: 0.5s;
      gap: 1em;
      height: 0;
      overflow: hidden;
    }

    .logo {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.344);
    }
    &:hover {
      .social-bar-links {
        padding-top: 1em;
        padding-bottom: 1em;
        height: 12em;
      }
    }
  }

  .footer {
    border-radius: 50px;
    padding-top: 100px;
    padding-bottom: 50px;
    margin-left: 8em;
    margin-right: 8em;
    margin-bottom: 25px;

    background: radial-gradient(
      circle at top center,
      #f7a0f5 5%,
      #1d95e7,
      #1c79f2,
      #0f42ce,
      #0f42ce,
      #0f42ce,
      #0f42ce
    );

    background-size: 190%;
    background-position: 0%;
    animation: colorChange 30s infinite ease alternate;
    h1 {
      color: white;
      font-size: 80px;
      font-weight: 700;
      font-family: "Grifter";
    }
    h6 {
      font-size: 20px;
      color: white;
      font-weight: 700;
      font-family: "Grifter";
      letter-spacing: 0.3px;
    }
    ul {
      font-size: 15px;
      list-style-type: none;
      color: var(--light-2);
      font-weight: 500;
      font-family: "Grifter";
      li {
        margin-top: 20px;
      }
    }
    h3 {
      font-size: 25px;
      color: white;
    }
    p {
      font-size: 15px;
      color: white;
      font-family: "Inter";
    }
    .company {
      margin-top: 100px;
      .left {
        text-align: left;
      }

      .right {
        text-align: right;
      }

      i {
        font-size: 25px;
        color: white;
        transition: 0.3s;
        &:hover {
          color: rgba(255, 255, 255, 0.408);
        }
      }
    }

    .big-font {
      font-size: 70px;
      font-family: "Inter";
    }

    .bold {
      font-weight: 700;
    }

    .cta-btn {
      border: none;
      padding: 24px 30px;

      margin-top: 0;

      justify-content: center;

      align-items: center;
      border-radius: 8px;
      background-color: #ffffff;
      -webkit-transition: 0.2s;
      transition: 0.2s;
      font-family: Grifter, sans-serif;
      color: #000;
      font-size: 17px;
      font-weight: 700;
      text-align: center;
      letter-spacing: 0.5px;
      text-decoration: none;
      box-shadow: 5px 5px 0 0 rgba(255, 255, 255, 0.349);
      &:hover {
        box-shadow: 8px 8px 0 0 rgba(255, 255, 255, 0.349);
        transform: translateY(-5px);
      }
    }
  }
}

.line {
  width: 100%;
  height: 1px;
  background: var(--light-1);
  margin-top: 1px;
  margin-bottom: 1px;
}

