      font-size: 22px;
      background: transparent;
    }
  }

  .about {
    padding-top: 150px;
    padding-bottom: 50px;
    background: radial-gradient(
      circle at left,
      #00c2ee33,
      transparent,
      transparent
    );
  }

  .footer {
    border-radius: 50px;
    padding-top: 100px;
    padding-bottom: 50px;
    margin-left: 1em;
    margin-right: 1em;
    margin-bottom: 25px;

    background: radial-gradient(
      circle at top center,
      #f7a0f5 0%,
      #1d95e7,
      #1c79f2,
      #0f42ce,
      #0f42ce,
      #0f42ce,
      #0f42ce
    );

    background-size: 190%;
    background-position: 0%;
    animation: colorChange 30s infinite ease alternate;
    h1 {
      color: white;
      font-size: 80px;
      font-weight: 700;
      font-family: "Grifter";
    }
    h6 {
      font-size: 20px;
      color: white;
      font-weight: 700;
      font-family: "Grifter";
      letter-spacing: 0.3px;
    }
    ul {
      font-size: 15px;
      list-style-type: none;
      color: var(--light-2);
      font-weight: 500;
      font-family: "Grifter";
      li {
        margin-top: 20px;
      }
    }
    h3 {
      font-size: 25px;
      color: white;
    }
    p {
      font-size: 15px;
      color: white;
      font-family: "Inter";
    }
    .company {
      margin-top: 100px;

      padding-left: 30px;
      padding-right: 40px;
      .left {
        text-align: center;
      }

      .right {
        text-align: center;
      }

      i {
        font-size: 25px;
        color: white;
        transition: 0.3s;
        &:hover {
          color: rgba(255, 255, 255, 0.408);
        }
      }
    }

    .big-font {
      font-size: 40px;
      font-family: "grifter";
    }

    .underline {
      margin-bottom: 100px;
      img {
        width: 50px;
      }
    }

    .bold {
      font-weight: 700;
    }

    .cta-btn {
      border: none;
      padding: 24px 30px;

      margin-top: 0;

      justify-content: center;

      align-items: center;
      border-radius: 8px;
      background-color: #ffffff;
      -webkit-transition: 0.2s;
      transition: 0.2s;
      font-family: Grifter, sans-serif;
      color: #0f43d4;
      font-size: 17px;
      font-weight: 700;
      text-align: center;
      letter-spacing: 0.5px;
      text-decoration: none;
      box-shadow: 5px 5px 0 0 rgba(255, 255, 255, 0.349);
      &:hover {
        box-shadow: 8px 8px 0 0 rgba(255, 255, 255, 0.349);
        transform: translateY(-5px);
      }
    }
  }
}

.modal-container {
  padding: 20px;
  font-family: "Inter";
  font-size: 17px;
  border: none;
  border-radius: 20px;
}

.pointer {
  cursor: pointer;
}

.small-icon {
  font-size: 15px;
  cursor: pointer;
}

/* fade out */
.fade-out {
  animation: fadeOut 1s ease-in-out;
  opacity: 0;
  transform: translate(-100px, -50%);
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* fade in */
.fade-in {
  animation: fadeIn 1s ease-in-out;
  opacity: 1;
  transform: translateX(0px, -50%);
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 var(--dark);
  }
  100% {
    box-shadow: 0 0 0 20px white;
  }
}
